* {
  font-family: "Roboto", sans-serif;
}

html, body {
  margin: 0;
  padding: 0;
}

#desktop {
  width: 100%;
  height: auto;
  background-color: #fff;
}

.wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 95px;
  top: 96px;
  margin: 0 40px;
}
.wrapper .content {
  display: flex;
  align-content: center;
  gap: 28px;
  flex-direction: column;
  height: auto;
  justify-content: center;
  width: 380px;
}
.wrapper .content .title {
  font-size: 42px;
  font-weight: 700;
  line-height: 120%;
  margin: 0;
}
.wrapper .content .copy {
  font-size: 16px;
  font-weight: 300;
  line-height: 150%;
}
.wrapper .content #btn {
  display: flex;
  align-items: center;
  border: none;
  border-radius: 32px;
  background-color: #E4C1AE;
  color: #FFF;
  padding: 16px 40px;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  width: 128px;
  height: 48px;
}

#alert {
  display: none;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
}
#alert.active {
  display: block;
}
#alert .wrapper__alert {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
}
#alert .wrapper__alert .conteiner__alert {
  display: flex;
  justify-content: space-between;
  background-color: #FFF;
  width: 920px;
  height: 210px;
}
#alert .wrapper__alert .conteiner__alert #exit__alert {
  cursor: pointer;
  width: 24px;
  height: 24px;
  margin: 40px;
}
#alert .wrapper__alert .conteiner__alert #reset__counter {
  display: none;
}
#alert .wrapper__alert .conteiner__alert #reset__counter.active {
  display: block;
  border-radius: 32px;
  border: none;
  background-color: #E4C1AE;
  color: #FFF;
  font-size: 16px;
  font-weight: 700;
  width: 115px;
  height: 38px;
  cursor: pointer;
}
#alert .wrapper__alert .content__alert {
  display: flex;
  flex-direction: column;
  gap: 24px;
  position: relative;
  left: 95px;
  top: 45px;
  width: 310px;
  margin: 0;
  height: 150px;
}
#alert .wrapper__alert .content__alert .title {
  font-size: 42px;
  padding: 0;
  margin: 0;
}
#alert .wrapper__alert .content__alert .copy {
  padding: 0;
  margin: 0;
  font-size: 16px;
  font-weight: 300;
}

.img-hero {
  width: 505px;
  height: 330px;
}

@media screen and (max-width: 1200px) {
  .wrapper {
    margin: 0 20px;
  }
  .wrapper .content {
    gap: 18px;
    width: 370px;
  }
  .wrapper .content .title {
    font-size: 32px;
  }
  .wrapper .content .copy {
    font-size: 15px;
  }
  .wrapper .content #btn {
    font-size: 15px;
    height: 38px;
  }
  .img-hero {
    width: 405px;
    height: 100%;
  }
}
@media screen and (max-width: 992px) {
  .wrapper {
    gap: 65px;
    margin: 0 10px;
  }
  .wrapper .content {
    gap: 8px;
    width: 370px;
  }
  .wrapper .content .title {
    font-size: 32px;
  }
  .wrapper .content .copy {
    font-size: 15px;
  }
  .wrapper .content #btn {
    font-size: 15px;
    height: 38px;
  }
  .img-hero {
    width: 355px;
    height: 100%;
  }
}
@media screen and (max-width: 780px) {
  .wrapper {
    flex-direction: column;
    gap: 25px;
    margin: 0 10px;
  }
  .wrapper .content {
    gap: 8px;
    width: 350px;
  }
  .wrapper .content .title {
    font-size: 32px;
  }
  .wrapper .content .copy {
    font-size: 15px;
  }
  .wrapper .content #btn {
    font-size: 15px;
    height: 38px;
  }
  .img-hero {
    width: 355px;
    height: 100%;
  }
  #alert .wrapper__alert .content__alert {
    display: flex;
    flex-direction: column;
    gap: 14px;
    position: relative;
    left: 35px;
    top: 35px;
    width: 280px;
    margin: 0;
  }
}